.AppHeader {
  height: 80px;
  line-height: 80px;
  text-align: center;
  color: #fff;
  position: relative;
  z-index: 100;
}
.AppHeader .sideBtn {
    position: absolute;
    left: 30px;
    top: 35px;
    width: 30px;
    height: 26px;
    cursor: pointer;
}
.AppHeader .sideBtn span, .AppHeader .sideBtn span:after, .AppHeader .sideBtn span:before {
      position: absolute;
      left: 0;
      width: 30px;
      height: 4px;
      content: '';
      background: #fff;
      -webkit-transition: all .1s;
      transition: all .1s;
}
.AppHeader .sideBtn span:after {
      -webkit-transform: translateY(10px);
      transform: translateY(10px);
}
.AppHeader .sideBtn span:before {
      -webkit-transform: translateY(-10px);
      transform: translateY(-10px);
}
.AppHeader input {
    font-size: 32px;
    width: 50%;
    text-align: center;
    border: none;
    outline: none;
    background: transparent;
    color: #fff;
    font-weight: bold;
}
.AppHeader ::-moz-placeholder {
    color: #fff;
}
.AppHeader ::-webkit-input-placeholder {
    color: #fff;
}
.AppHeader :-ms-input-placeholder {
    color: #fff;
}
.AppHeader ::-moz-selection {
    background: red;
}
.AppHeader ::selection {
    background: red;
}
.AppHeader ::-moz-selection {
    background: red;
}

.sidebar {
  width: 100%;
  min-height: 100vh;
}
.sidebar .mainMenu {
    padding-top: 140px;
    padding-left: 20px;
    padding-right: 20px;
}
.sidebar .mainMenu li {
      -webkit-transition: all ease .3s;
      transition: all ease .3s;
      opacity: 0;
      overflow: hidden;
}
.sidebar .mainMenu li input {
        display: none;
}
.sidebar .mainMenu li a {
        display: inline-block;
        width: 100%;
        height: 60px;
        border-radius: 5px;
        line-height: 60px;
        text-align: center;
        color: #fff;
        font-size: 24px;
        margin-bottom: 30px;
}
.sidebar .mainMenu li a:hover {
          opacity: .8;
}
.sidebar .mainMenu li.delayshow {
        -webkit-animation: btnshows forwards 1s;
                animation: btnshows forwards 1s;
        -webkit-animation-delay: .2s;
                animation-delay: .2s;
}
@-webkit-keyframes btnshows {
from {
    opacity: 0;
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
}
to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}
@keyframes btnshows {
from {
    opacity: 0;
    -webkit-transform: translateX(-30px);
            transform: translateX(-30px);
}
to {
    opacity: 1;
    -webkit-transform: translateX(0);
            transform: translateX(0);
}
}

